@import "https://unpkg.com/modern-css-reset/dist/reset.min.css";
@import "https://unpkg.com/@contentful/f36-tokens@4.0.1/dist/css/index.css";
@import "https://www.contentful.com/fonts.css";
body, html {
  margin: 0;
  padding: 0;
}

html {
  font-size: 100%;
}

a {
  color: var(--color-text);
  font-weight: 700;
}

body {
  -webkit-font-smoothing: antialiased;
  color: var(--text-color);
  background: var(--bkg);
  font-family: Avenir Next W01;
  line-height: var(--line-height-default);
  margin: 0;
}

button {
  border-radius: var(--border-radius-medium);
  font-weight: var(--font-weight-medium);
  line-height: var(--line-height-condensed);
  padding: var(--spacing-2xs) var(--spacing-m);
  min-height: 32px;
  cursor: pointer;
  color: var(--color-white);
  background: var(--blue-500);
  border: 1px solid;
  border-color: var(--blue-500);
  outline: none;
  text-decoration: none;
  box-shadow: 0 1px #19253214;
}

button:hover {
  background: var(--blue-600);
  border-color: var(--blue-600);
}

button:focus {
  border-color: var(--blue-600);
  box-shadow: var(--glow-primary);
}

button:focus:not(:focus-visible) {
  border-color: var(--color-blue500);
  box-shadow: unset;
}

button:focus-visible {
  border-color: var(--color-blue-600);
  box-shadow: var(--glow-primary);
}

button > a {
  font-weight: normal;
  text-decoration: none;
}

button.button-secondary {
  background: var(--gray-200);
  color: var(--gray-900);
  background: var(--color-white);
  border-color: var(--gray-300);
}

button.button-large {
  font-weight: var(--font-size-xl);
  line-height: var(--line-height-xl);
  min-height: 48px;
  padding: var(--spacing-xs) var(--spacing-m);
}

h1, h2, h3, h4, h5 {
  margin-bottom: var(--spacing-l);
}

h1, h2, h3 {
  letter-spacing: -.02rem;
  font-weight: 800;
}

h1 {
  font-size: var(--font-size-4xl);
}

h2 {
  font-size: var(--font-size-3xl);
}

h3 {
  font-size: var(--font-size-2xl);
}

h4 {
  font-size: var(--font-size-xl);
}

h5 {
  font-size: var(--font-size-l);
}

p {
  font-size: var(--font-size-l);
  line-height: var(--line-height-m);
  margin: var(--spacing-m) 0;
}

small {
  letter-spacing: .08em;
  text-transform: uppercase;
  margin-bottom: 8px;
  font-size: .8rem;
  font-weight: 500;
  line-height: 1.1;
  display: block;
}

.nav-spacer, .nav-menu {
  flex: 1;
  justify-content: center;
}

.nav-menu {
  text-align: left;
}

.nav-menu span {
  padding-left: 20px;
}

.nav-menu a {
  color: #fff;
  padding: 10px;
}

.event {
  width: 100%;
  max-width: 1280px;
  flex-flow: wrap;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 40px;
  display: flex;
}

.event .event-info {
  width: 100%;
  padding-top: 20px;
  padding-left: 20px;
  padding-right: 20px;
}

.event .event-image-area {
  max-width: 400px;
  max-height: 250px;
  width: 100%;
  justify-content: center;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.event .event-image-area .event-image-container {
  width: 100%;
  max-height: 250px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.event .event-image-container img {
  min-width: 100%;
  min-height: 100%;
  flex-shrink: 0;
}

.event .event-image-container .event-time-date {
  text-align: center;
  background: #fff;
  padding: 10px 5px 0;
  position: absolute;
  top: 10px;
  right: 10px;
}

.event .event-image-container .event-time-date .month {
  font-size: 14px;
}

.event .event-image-container .event-time-date .day {
  padding-bottom: 2px;
  font-size: 22px;
}

.event .event-image-container .event-time-date .time {
  border-top: 1px solid #d3d3d3;
  padding: 5px;
  font-size: 12px;
}

.contact-form * {
  display: block;
}

.contact-form button {
  margin-top: 20px;
}

.aspect-ratio {
  width: 50%;
  height: 0;
  padding-bottom: 50%;
  display: inline-block;
  position: relative;
}

.aspect-ratio iframe {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.center-vert {
  justify-content: center;
  align-items: center;
}

.river.center-hori {
  text-align: center;
}

.river table {
  display: inline-block;
}

.river table p {
  margin: 0;
}

.river table td:not(:last-child) {
  text-align: left;
  padding: 0 40px 0 0;
}

.river table td:last-child {
  text-align: right;
}

@keyframes moveBanner {
  0% {
    background-position: center;
  }

  25% {
    background-position: 0 100%;
  }

  50% {
    background-position: center;
  }

  75% {
    background-position: 100% 0;
  }
}

.banner-img {
  width: 100%;
  height: 500px;
  text-align: center;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  animation: 40s linear infinite moveBanner;
}

.grid {
  grid-gap: var(--spacing-xs);
  grid-template-columns: repeat(auto-fill, minmax(360px, 1fr));
  display: grid;
}

.painting {
  text-align: center;
  box-sizing: border-box;
  border-bottom: 1px solid var(--gray-300);
  margin: 5px;
}

.painting h4 {
  margin-bottom: 0;
}

.painting p {
  margin-top: 0;
}

.painting img {
  width: 100%;
  height: 350px;
  object-fit: cover;
}

.river {
  max-width: 1280px;
  text-align: center;
  flex-direction: column;
  justify-content: space-between;
  gap: 32px;
  margin-top: 3rem;
  margin-left: auto;
  margin-right: auto;
  padding: 0 1rem;
  display: flex;
}

.contain {
  max-width: 1280px;
  flex-direction: column;
  justify-content: space-between;
  gap: 32px;
  margin-top: 3rem;
  margin-left: auto;
  margin-right: auto;
  padding: 0 1rem;
  display: flex;
}

.contain article, .river article {
  width: 100%;
}

.river article button:first-child {
  margin-right: var(--spacing-m);
}

.river > img {
  width: 100%;
  max-width: 550px;
  max-height: 350px;
  object-fit: cover;
}

.tags {
  justify-content: center;
  margin-top: 3rem;
  display: flex;
}

.tags label {
  cursor: pointer;
}

@media screen and (min-width: 40em) {
  .event .event-info {
    width: 50%;
    padding-top: 0;
    padding-left: 20px;
  }

  .event .event-image-area, .contain article {
    width: 50%;
  }

  .river:nth-child(2n) {
    flex-direction: row-reverse;
  }

  .river:nth-child(2n+1) {
    flex-direction: row;
  }

  .river {
    text-align: left;
  }

  .river article, .river > img {
    width: 50%;
  }
}

